
<template>
<div class="upId">
    <div class="upId-title">身份认证</div>
    <div class="upId-input">
      <van-cell-group>
        <van-field v-model="name" :placeholder="$t('EnterName')"   @blur="inputName" :error-message="nameError">
          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/name.png" alt="">
        </van-field>
        <van-field v-model="cardID " :placeholder="$t('EnterID')"  :error-message="idError" @blur="inputButton">
          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/id.png" alt="">
        </van-field>
      </van-cell-group>
    </div>
    <div class="upId-upImg">
      <div class="upId-upImg-box">
        <van-uploader preview-image :after-read="upIdPositive">
          <img  class="uploader-img" src="../../../assets/registerIcon/upImgBox.png" />
          <img ref="idPositive" class="uploader-img2" src="../../../assets/registerIcon/upId.png" />
        </van-uploader>
      </div>
      <div class="uploader-text">点击上传身份证正面</div>
      <div class="upId-upImg-box">
        <van-uploader  accept="image/*" :after-read="upIdBack">
          <img  class="uploader-img" src="../../../assets/registerIcon/upImgBox.png" />
          <img ref="idBack" class="uploader-img2" src="../../../assets/registerIcon/idBack.png" />
        </van-uploader>
      </div>
      <div class="uploader-text">点击上传身份证反面</div>
    </div>
 <div class="upId-Tips">
  <p>温馨提示</p>
    <p>1.请确保身份证边框完整、文字清晰、无反光</p>
   <p>2.请确保身份证未过期、与开户本人一致</p>
   <p>3.请使用身份证原件拍摄,本平台会加密存储您的身信息,仅用于开户使用</p>
  </div>
   <div class="upId-but" @click="linkUpId">下一步</div>
 </div>
</template>

