<script>
  import { IdentityCodeValid } from '../../../utils/verificationId'
  import Exif from 'exif-js'
  import { Toast } from 'vant';
  export default {
  name: 'upId',
  data () {
    return {
      // 身份证号码
      cardID: '',
      // 接收上级参数
      name:'',
      nameError:'',
      phone:'',
      code:'',
      keying:'',
      // 图片上传
      // 上传身份证正面
      // 正反上传区分
      FrB:'',
      files: {
        name: "",
        type: ""
      },
      headerImage: null,
      picValue: null,
      backPicValue: null,
      upImgUrl:'http://121.43.137.150:7777/api/file/uploadImg',
      idFrontName:'',
      idBackName:'',
      idError:'',
      showBut:false,
      // 开户行名称
      bankCardName: '',
      // 银行卡号
      bankCardId: '',
      value: '',
      showPicker: false,
      columns: ['Binance-BTC', 'Binance-USDT'],
    }
  },
  created(){
    // this.name = this.$route.params.name
    // this.phone = this.$route.params.phone
    // this.code = this.$route.params.code
    // this.keying = this.$route.params.keying
  },
  methods: {
    // 验证账号
    inputName(){
      let value = this.name
      let reg = /^[\u4E00-\u9FA5]/g
      let val = reg.test(value)
      if (val) {
        this.nameError = ''
      } else {
        this.nameError = '姓名要求为中文字符请重新输入！'
      }
    },
    inputButton () {
      console.log(IdentityCodeValid(this.cardID))
      if(IdentityCodeValid(this.cardID) === true){
        this.showBut = true
        this.idError = ''
      }else {
        this.showBut = false
        this.idError = IdentityCodeValid(this.cardID)
      }
    },
    // 验证身份证
    cardIDBlur(){
      if(IdentityCodeValid(this.cardID) != true){
        this.idError = true
        this.cardID = IdentityCodeValid(this.cardID)
      }else {
      }
    },
    cardIDFocus(){
      if(this.idError){
        this.cardID = ''
        this.idError = false
      }
    },
    // 图片上传后的回调函数--身份证正面
    upIdPositive (file) {
      this.$refs.idPositive.src = file.content
      this.files.name = file.file.name; // 获取文件名
      this.files.type = file.file.type; // 获取类型
      this.picValue = file.file; // 文件流
      this.imgPreview(this.picValue);
      this.FrB = 0
      Toast.loading({
        message:'图片上传中...',
        duration:0,
      })
    },
    // 图片上传后的回调函数--身份证反面
    upIdBack (file) {
      this.$refs.idBack.src = file.content
      this.files.name = file.file.name; // 获取文件名
      this.files.type = file.file.type; // 获取类型
      this.backPicValue = file.file; // 文件流
      this.imgPreview(this.backPicValue);
      this.FrB = 1
      Toast.loading({
        message:'图片上传中...',
        duration:0,
      })
    },
    linkUpId () {
      if(this.name !== '' && this.cardID !== '' && this.idFrontName !== '' && this.idBackName !== '' && this.nameError === '' && this.showBut === true){
        this.$router.push({
          name: 'upBankCard',
          params:{
            name:this.name,
            phone:this.phone,
            code:this.code,
            keying:this.keying,
            cardID:this.cardID,
            idFrontName:this.idFrontName,
            idBackName:this.idBackName
          }
        })
      }else{
        Toast.fail({
        message:'信息有误或漏填'
      })
      }
    },
    // 图片处理
    imgPreview(file) {
      let self = this;
      let Orientation;
      //去获取拍照时的信息，解决拍出来的照片旋转问题
      Exif.getData(file, function () {
        Orientation = Exif.getTag(this, "Orientation");
      });
      // 看支持不支持FileReader
      if (!file || !window.FileReader) return;
      if (/^image/.test(file.type)) {
        // 创建一个reader
        let reader = new FileReader();
        // 将图片2将转成 base64 格式
        reader.readAsDataURL(file);
        // 读取成功后的回调
        reader.onloadend = function () {
          // console.log(this.result);
          let result = this.result;
          let img = new Image();
          img.src = result;
          //判断图片是否大于500K,是就直接上传，反之压缩图片
          if (this.result.length <= 500 * 1024) {
            self.headerImage = this.result;
            self.postImg();
          } else {
            img.onload = function () {
              let data = self.compress(img, Orientation);
              self.headerImage = data;
              self.postImg();
            };
          }
        };
      }
    },
    // 压缩图片
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //瓦片canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      // let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
      let ratio;
      if ((ratio = (width * height) / 4000000) > 1) {
        // console.log("大于400万像素");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      //        铺底色
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //如果图片像素大于100万则使用瓦片绘制
      let count;
      if ((count = (width * height) / 1000000) > 1) {
        // console.log("超过100W像素");
        count = ~~(Math.sqrt(count) + 1); //计算要分成多少块瓦片
        //            计算每块瓦片的宽和高
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //修复ios上传图片的时候 被旋转的问题
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //需要顺时针（向左）90度旋转
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //需要逆时针（向右）90度旋转
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //需要180度旋转
            this.rotateImg(img, "right", canvas); //转两次
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //进行最小压缩
      let ndata = canvas.toDataURL("image/jpeg", 0.3);
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    },
    // 旋转图片
    rotateImg(img, direction, canvas) {
      //最小与最大旋转方向，图片旋转4次后回到原方向
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //img的高度和宽度不能在img元素隐藏后获取，否则会出错
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //旋转到原位置，即超过最大值
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //旋转角度以弧度值为参数
      let degree = (step * 90 * Math.PI) / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    //将base64转换为文件
    dataURLtoFile(dataurl) {
      var arr = dataurl.split(","),
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], this.files.name, {
        type: this.files.type
      });
    },
    //这里写接口
    postImg() {
      let file = this.dataURLtoFile(this.headerImage);
      let formData = new window.FormData();
      formData.append("uploadFile", file);
      this.$axios.post(this.upImgUrl, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      }).then(res =>{
        if(this.FrB === 0){
          this.idFrontName = res.data.object
          Toast.clear()
          console.log(this.idFrontName,'身份证正面')
        }else if(this.FrB === 1){
          this.idBackName = res.data.object
          Toast.clear()
          console.log(this.idBackName,'身份证反面')
        }
      })
    },
    onConfirm(value) {
      console.log(value,'value')
      this.value = value;
      this.showPicker = false;
    },
    // 申请开户
    getLogin(msg) {
      let that = this;
      console.log(that.global.ws);
      if (that.global.ws && that.global.ws.readyState === 1) {
        console.log("发送信息", msg);
        that.global.ws.onopen = that.onopen()
      }else {
        Toast({
          position:'bottom',
          duration:'2000',
          message:'超出安全时间请重新开户',
          onClose () {
            that.$router.push({
              name:'register',
            })
          }
        })
      }
      that.global.ws.onmessage = function(res) {
        that.websocketonmessage (res)
      };
    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
      let openKeying = localStorage.getItem('openKeying')
      actions = {
        action: "certification",
        reqID: 9,
        data: {
          name:this.name,
          phone:'',
          code:'',
          keying:'',
          idcard:'',
          bank:this.value,
          bankAccount:this.bankCardId,
          IdCardIs:'',
          IdCardBack:'',
          BankCardImg:''
        }
      } // 请根据实际项目需要进行修改
      console.log(JSON.stringify(actions))
      that.global.ws.send(JSON.stringify(actions))
    },
    websocketonerror () { // 连接建立失败重连
      this.getLogin()
    },
    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata,'开户')
      let _this = this
      if(this.redata.action === "certification"){
        localStorage.removeItem('openKeying')
        if(this.redata.data.code === 0  ){
          localStorage.setItem('status',3)
          Toast({
            message:_this.$t('submitOpenS'),
            duration:2000,
            position:'bottom',
            onClose(){
              _this.$router.push({
                name:'toBeAudited',
              })
            }
          })
        }else {
          Toast({
            message:_this.$t('submitOpenF'),
            duration:3000,
            position:'bottom',
            onClose(){
              _this.$router.push({
                name:'register',
              })
            }
          })
        }
        // else {
        //     Toast({
        //       message:'提交审核失败，请重新提交',
        //       duration:3000,
        //       onClose(){
        //         _this.$router.push({
        //           name:'register',
        //         })
        //       }
        //     })
        //   }
      }
      if (this.redata.data.ok) {
        localStorage.setItem('Flag', 'isLogin')
        localStorage.setItem('account', _this.account)
        sessionStorage.setItem("disc",this.environment);
        this.$router.push({
          path:'home'
        })
      } else {
      }
    },
    websocketclose (e) { // 关闭
      console.log('断开连接', e)
    },
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/register";
</style>



